import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: "#041620",
      color: theme.palette.common.white,
    },
    body: {
      fontSize: 14,
    },
  }))(TableCell);
  
  const StyledTableRow = withStyles((theme) => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
      },
    },
  }))(TableRow);

  const useStyles = makeStyles({
    table: {
      minWidth: 500,
    },
  });

export default function Download() {
    const [data, setData] = useState([]);
    const classes = useStyles();

    useEffect(() => {
        axios.get('https://ec2-3-143-113-114.us-east-2.compute.amazonaws.com:8000/convert/', {
            params: {
                email: localStorage.getItem('email')
            }
        })
        .then(res => {
            setData(res.data);
        })
    }, []);

    return (
        <div 
            style={{
                display: "flex", textAlign: "center", justifyContent: "start", flexDirection: "column",
                flexWrap: "nowrap", padding: 10, height: "100vh",
                backgroundImage: "linear-gradient(to top right, #2af598, #009efd)", 
            }}
        >
            {
                data.length ?
                <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="customized table">
                        <TableHead>
                        <TableRow>
                            <StyledTableCell>Uploaded Filename</StyledTableCell>
                            <StyledTableCell align="right">Created At</StyledTableCell>
                            <StyledTableCell align="right">Download</StyledTableCell>
                        </TableRow>
                        </TableHead>
                        <TableBody>
                        {data.map((d) => (
                            <StyledTableRow key={d.id}>
                                <StyledTableCell component="th" scope="row">
                                    {d.fields.fileName}
                                </StyledTableCell>
                                <StyledTableCell align="right">{d.fields.created_at}</StyledTableCell>
                                <StyledTableCell align="right">
                                    {
                                        d.fields.geojson ? 
                                        <a href={`https://farmin.s3.us-east-2.amazonaws.com/geojsons/${d.fields.fileName.substr(0, d.fields.fileName.lastIndexOf(".")) + ".geojson"}`}>Click Here</a>
                                        : "Not Available"
                                    }
                                </StyledTableCell>
                            </StyledTableRow>
                        ))}
                        </TableBody>
                    </Table>
                </TableContainer>
                : 
                <p style={{fontStyle: "italic", display: "flex", width: "100%", height: "100%", alignItems: "center", justifyContent: "center"}}>No data found</p>
            }           
            
        </div>
    )
}
