import axios from 'axios';
import * as actionTypes from './actionTypes';

export const authStart = () => {
    return {
        type: actionTypes.AUTH_START
    }
}

export const authSuccess = token => {
    return {
        type: actionTypes.AUTH_SUCCESS,
        token: token
    }
}

export const authFail = error => {
    return {
        type: actionTypes.AUTH_FAIL,
        error: error
    }
}

export const logout = () => {
    localStorage.removeItem('user');
    localStorage.removeItem('expirationDate');
    localStorage.removeItem('email');
    return {
        type: actionTypes.AUTH_LOGOUT
    }
}

export const checkAuthTimeout = expirationDate => {
    return dispatch => {
        setTimeout(() => {
            dispatch(logout());
        }, expirationDate * 1000)
    }
}

export const authLogin = (email, password) => {
    return dispatch => {
        dispatch(authStart());
        axios.post('http://ec2-3-143-113-114.us-east-2.compute.amazonaws.com:8000/rest-auth/login/', {
            email: email,
            password: password
        })
        .then(res => {
          const token  = res.data.key;
          const expirationData = new Date(new Date().getTime() + 3600 * 1000)
          localStorage.setItem('token', token);
          localStorage.setItem('expirationDate', expirationData);
          localStorage.setItem('email', email);
          dispatch(authSuccess(token));
          dispatch(checkAuthTimeout(3600));
        })
        .catch(err => {
            dispatch(authFail(err))
        })
    }
}

export const authSignup = (
    email, 
    password1, 
    password2, 
    firstname,
    lastname,
    companyname,
    country
    ) => {
    return dispatch => {
        dispatch(authStart());
        axios.post('http://ec2-3-143-113-114.us-east-2.compute.amazonaws.com:8000/rest-auth/registration/', {
            email: email,
            password1: password1,
            password2: password2,
            firstname: firstname,
            lastname: lastname,
            companyname: companyname,
            country: country
        })
        .then(res => {
          const token  = res.data.key;
          const expirationData = new Date(new Date().getTime() + 3600 * 1000)
          localStorage.setItem('token', token);
          localStorage.setItem('expirationDate', expirationData);
          localStorage.setItem('email', email);
          dispatch(authSuccess(token));
          dispatch(checkAuthTimeout(3600));
        })
        .catch(err => {
            dispatch(authFail(err))
        })
    }
}

export const authCheckState = () => {
    return dispatch => {
        const token = localStorage.getItem('token');
        if (token === undefined) {
            dispatch(logout());
        } else {
            const expirationData = new Date(localStorage.getItem('expirationDate'));
            if (expirationData <= new Date()) {
                dispatch(logout());
            } else {
                dispatch(authSuccess(token));
                dispatch(checkAuthTimeout( (expirationData.getTime() - new Date().getTime()) / 1000 ));
            }
        }
    }
}
