import React, { useRef, useState, useEffect } from 'react';
import AWS from 'aws-sdk';
import axios from 'axios';
import ClipLoader from "react-spinners/ClipLoader";
import add from '../images/add.png';
import { makeStyles } from '@material-ui/core/styles';
import { FaUpload } from "react-icons/fa";

const S3_BUCKET = 'farmin/tiff-uploads';
const REGION = 'us-east-2';

AWS.config.update({
    accessKeyId: 'AKIAV5NSGS4LEEJAQ6F7',
    secretAccessKey: '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'
})

const myBucket = new AWS.S3({
    params: { Bucket: S3_BUCKET},
    region: REGION,
})

const useStyles = makeStyles({
    wrapper: {
        // width: "100%",
        height: "50%",
        display: "flex",
        alignItems: "center",
        justifyContent: "center"
    },
    fileUpload: {
        height: 200,
        width: 200,
        borderRadius: 100,
        position: "relative",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",

        border: "4px solid #FFFFFF",
        overflow: "hidden",
        backgroundImage: "linear-gradient(to bottom, #041620 50%, #FFFFFF 50%)",
        backgroundSize: "100% 200%",
        transition: "all 1s",
        color: "#FFFFFF",
        fontSize: 100,
        "&:hover": {
            backgroundPosition: "0 -100%",
            color: "#041620"
        }
    },
    input: {
        height: 200,
        width: 200,
        position: "absolute",
        top: 0,
        left: 0,
        opacity: 0,
        cursor: "pointer"
    },
    startButton: {
        background: "transparent", borderRadius: 3, border: "2px solid #041620",
        padding: "15px 20px", fontWeight: 700, transition: "0.3s ease-in-out",
        marging: "initial", textDecoration: "none", 
        cursor: "pointer", display: "block", wordWrap: "break-word", userSelect: "none",
        fontFamily: "Saira Semi Condensed", fontSize: 18,
        "&:hover": {
            backgroundColor: "#041620",
            color: "#fff"
        }
    }
  });

export default function Upload() {
    const [progress , setProgress] = useState(0);
    const [selectedFile, setSelectedFile] = useState(null);
    const [image, setImage] = useState(null);
    const [uploading, setUploading] = useState(false);
    const [fileName, setFileName] = useState(null);

    const classes = useStyles();

    const handleFileInput = (e) => {
        e.preventDefault();
        setSelectedFile(e.target.files[0]);
        // console.log(e.target.files[0])
    }

    const uploadFile = (file) => {
        // console.log(file);
        setFileName(file.name.substr(0, file.name.lastIndexOf(".")) + ".geojson");
        const params = {
            ACL: 'public-read',
            Body: file,
            Bucket: S3_BUCKET,
            Key: file.name
        };
        myBucket.putObject(params)
        .on('httpUploadProgress', (evt) => {
            setProgress(Math.round((evt.loaded / evt.total) * 100))
        })
        .send((err) => {
            if (err){
                console.log(err);
                alert(err);
                setSelectedFile(null);
                setImage(null);
                setUploading(false);
                setFileName(null);
            } 
        })
        setUploading(true);    

        const formData = new FormData();
        formData.append('image', file);
        formData.append('fileName', file.name);
        formData.append('email', localStorage.getItem('email'));
        // ec2-3-143-113-114.us-east-2.compute.amazonaws.com
        axios.post('https://ec2-3-143-113-114.us-east-2.compute.amazonaws.com:8000/convert/', formData, {
            headers: {
            'Content-Type': 'multipart/form-data'
            }
        })
        .then(res => {
            fetch("https://ec2-3-143-113-114.us-east-2.compute.amazonaws.com:8000/media/convert/" + res.data)
            .then(res => res.blob()) // Gets the response and returns it as a blob
            .then(blob => {
                var objectURL = URL.createObjectURL(blob);
                setImage(objectURL);
                // console.log("Done");
                setUploading(false);
            });
        })
        .catch((err) => {
            console.log(err); 
            alert(err);
            setSelectedFile(null);
            setImage(null);
            setUploading(false);
            setFileName(null);
        } )    
    }

    return (
        <div 
            style={{
                display: "flex", textAlign: "center", justifyContent: "start", flexDirection: "column",
                flexWrap: "nowrap",
                backgroundImage: "linear-gradient(to top right, #2af598, #009efd)", height: "100vh",
                animation: "backgroundAnimation 10s ease-in-out infinite",
            }}
        >
            {
                image &&
                <div >
                    <img src={image} style={{ width: 500, marginBottom: 5, paddingTop: 10 }}/>
                </div>
            }
            {/* { 
                !uploading &&
                <p>Click <a href={`https://farmin.s3.us-east-2.amazonaws.com/geojsons/${fileName}`}>Here</a> to download the geojson file</p>
            } */}
            {/* <div style={{display: "flex", textAlign: "center", justifyContent: "center"}}>
                <span
                    style={{
                        userSelect: "none", width: 400, height: 200, boxShadow: "black 0 0 0 2px inset",
                        borderRadius: 4, marginBottom: 6, display: "flex", flexDirection: "column",
                        alignItems: "center", justifyContent: "center", outline: "none"
                    }}
                >
                    <img src={add} style={{ maxWidth: 60, maxHeight: 60, marginBottom: 10, userSelect: "none"}} />
                    <p style={{marginTop: 2, marginBottom: 0}}>Select tiff image</p>
                    <input type="file" onChange={handleFileInput} disabled={uploading} style={{ width: "50%", outline: "none", cursor: "pointer", display: "flex", textAlign: "center", color: "#000"}}/>
                </span>
            </div> */}
            <div>
                <h2 style={{fontFamily: "Saira Semi Condensed"}}>Upload tiff Image</h2>
            </div>
            <div className={classes.wrapper}>
                <div className={classes.fileUpload}>
                    <input type="file" onChange={handleFileInput} disabled={uploading} className={classes.input} />
                    <FaUpload />
                </div>
                
            </div>
            <div style={{display: "flex", textAlign: "center", justifyContent: "center",}}>
                <button 
                    onClick={() => uploadFile(selectedFile)} 
                    disabled={uploading || !selectedFile}
                    className={classes.startButton}
                >
                    Start Processing</button>
            </div>
            {
            uploading &&
            <div style={{display: "flex", textAlign: "center", justifyContent: "center"}}>
                <ClipLoader color={"#ffffff"} loading={true} size={25} />
                <div style={{display: "flex", textAlign: "center", justifyContent: "center", paddingRight: 5}}>
                    Please wait while your image is being processed
                    {/* File Upload Progress is {progress}% Complete */}
                </div>
            </div>
            }
        </div>
    )
}
