import React, { Component } from 'react';
import MapGL from 'react-map-gl';
import { Editor, DrawPolygonMode, EditingMode } from 'react-map-gl-draw';
import * as config from '../config';
import { getFeatureStyle, getEditHandleStyle } from './DrawStyle';
import axios from 'axios';

import { Image } from 'antd'
import DriveEtaIcon from '@material-ui/icons/DriveEta';
import OpacityIcon from '@material-ui/icons/Opacity';
import LocalDrinkIcon from '@material-ui/icons/LocalDrink';

const TOKEN = config.REACT_APP_TOKEN

class Draw extends Component {
  constructor(props) {
    super(props);
    this._editorRef = null;
    this.state = {
      viewport: {
        latitude: 24.76732136369363,
        longitude: 54.679060885419396,
        zoom: 12
      },
      mode: null,
      selectedFeatureIndex: null,
      hideCars: true,
      carsFilename: null,
      processingCars: null,
      hideShips: true,
      shipsFilename: null,
      processingShips: null,
      hideSpills: true,
      spillsFilename: null,
      processingSpills: null
    };
  }
  
  processCars = () => {
    this.setState({processingCars: true})

    var dataUrl = this.mapInstance.getCanvas().toDataURL()

    axios.post('https://ec2-3-143-113-114.us-east-2.compute.amazonaws.com:8000/cars/', {
      dataUrl
    })
    .then(res => {
      alert("Cars Processing Successful")
      this.setState({processingCars: false})
      this.setState({carsFilename: res.data}) 
      this.setState({hideCars: false})
    }).catch((err) => console.log(err))
  }

  processShips = () => {
    this.setState({processingShips: true})

    var dataUrl = this.mapInstance.getCanvas().toDataURL()
    
    axios.post('https://ec2-3-143-113-114.us-east-2.compute.amazonaws.com:8000/ships/', {
      dataUrl
    })
    .then(res => {
      alert("Ships Processing Successful")
      this.setState({processingShips: false})
      this.setState({shipsFilename: res.data}) 
      this.setState({hideShips: false})
    })
    .catch((err) => console.log(err))
  }

  processSpills = () => {
    this.setState({processingSpills: true})

    var dataUrl = this.mapInstance.getCanvas().toDataURL()

    axios.post('https://ec2-3-143-113-114.us-east-2.compute.amazonaws.com:8000/spills/', {
      dataUrl
    })
    .then(res => {
      alert("Spills Processing Successful")
      this.setState({processingSpills: false})
      this.setState({spillsFilename: res.data}) 
      this.setState({hideSpills: false})
    })
    .catch((err) => console.log(err))
  }

  _updateViewport = viewport => {
    this.setState({viewport});
  };

  _onSelect = options => {
    this.setState({selectedFeatureIndex: options && options.selectedFeatureIndex});
  };

  _onDelete = () => {
    const selectedIndex = this.state.selectedFeatureIndex;
    if (selectedIndex !== null && selectedIndex >= 0) {
      this._editorRef.deleteFeatures(selectedIndex);
    }
  };

  _onUpdate = ({editType}) => {
    if (editType === 'addFeature') {
      this.setState({
        mode: new EditingMode()
      });
    }
    // this.mapRef.getMap()._canvas.toDataURL('image/png');
  };

  _renderDrawTools = () => {
    // copy from mapbox
    return (
      <div className="mapboxgl-ctrl-top-left">
        <div className="mapboxgl-ctrl-group mapboxgl-ctrl">
          <button
            className="mapbox-gl-draw_ctrl-draw-btn mapbox-gl-draw_polygon"
            title="Polygon tool (p)"
            onClick={() => this.setState({mode: new DrawPolygonMode()})}
          />
          <button
            className="mapbox-gl-draw_ctrl-draw-btn mapbox-gl-draw_trash"
            title="Delete"
            onClick={this._onDelete}
          />
        </div>
      </div>
    );
  };

  componentDidMount() {
    this.mapInstance = this.mapRef.getMap()
  }

  render() {
    const {
      viewport, 
      mode, 
      hideCars, 
      carsFilename, 
      hideShips, 
      shipsFilename, hideSpills, spillsFilename } = this.state;

    return (
      <MapGL
        ref={ map => this.mapRef = map}
        {...viewport}
        width="100%"
        height="100%"
        mapStyle="mapbox://styles/hassanms/ckhakjywz52lh19of76vsm4wn"
        mapboxApiAccessToken={TOKEN}
        onViewportChange={this._updateViewport}
        preserveDrawingBuffer={true}
      >
        <button id="cars" style={{position: 'absolute', top: 100, left: 10}} onClick={() => this.processCars()} title="Detect Cars" disabled={this.state.processingCars}>
          <DriveEtaIcon fontSize="large" />
        </button>

        <button id="ships" style={{position: 'absolute', top: 150, left: 10}} onClick={() => this.processShips()} title="Detect Ships" disabled={this.state.processingShips}>
          <OpacityIcon fontSize="large" />
        </button>

        <button id="spills" style={{position: 'absolute', top: 200, left: 10}} onClick={() => this.processSpills()} title="Detect Spills" disabled={this.state.processingSpills}>
          <LocalDrinkIcon fontSize="large" />
        </button>

        {!hideCars && <Image
          style={{float: "right"}}
          width={100}
          src={"https://ec2-3-143-113-114.us-east-2.compute.amazonaws.com:8000/media/cars/" + carsFilename}
        />}

        {!hideShips && <Image
          style={{float: "right"}}
          width={100}
          src={"https://ec2-3-143-113-114.us-east-2.compute.amazonaws.com:8000/media/ships/" + shipsFilename}
        />}

        {!hideSpills && <Image
          style={{float: "right"}}
          width={100}
          src={"https://ec2-3-143-113-114.us-east-2.compute.amazonaws.com:8000/media/spills/" + spillsFilename}
        />}

        <Editor
          ref={_ => (this._editorRef = _)}
          style={{width: '100%', height: '100%'}}
          clickRadius={12}
          mode={mode}
          onSelect={this._onSelect}
          onUpdate={this._onUpdate}
          editHandleShape={'circle'}
          featureStyle={getFeatureStyle}
          editHandleStyle={getEditHandleStyle}
        />
        {this._renderDrawTools()}
        {/* {this._renderControlPanel()} */}
      </MapGL>
    );
  }
}

export default Draw;